import React from 'react';
import { Helmet } from 'react-helmet-async';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { useNavigate } from 'react-router-dom';

//firestore
import { useFirebaseAuth } from 'src/contexts/FirebaseAuthProvider';
import { collection, getDocs } from "firebase/firestore"; 
import { ProductInterface, convertProductQuerySnapshots } from 'src/models/products';

//icons
import AddIcon from '@mui/icons-material/Add';

//components
import ProductCard from './ProductCard';

function Products() {
    const { database } = useFirebaseAuth();
    const [butilProducts, setButilProducts] = React.useState<ProductInterface[]>([]);
    const navigate = useNavigate();

    React.useEffect(() => {
        async function getProducts() {
            let productList: ProductInterface[] = [];
            const querySnapshot = await getDocs(collection(database, "products"));

            querySnapshot.forEach((doc) => {
                const currentProduct = convertProductQuerySnapshots(doc);
                productList.push(currentProduct.getProduct());
            });

            setButilProducts(state => productList);
        }

        getProducts();
    }, [database]);

    return (
        <>
            <Helmet>
                <title>Products</title>
            </Helmet>
            <Container sx={{ mb: 5 }}>
                <Box sx={{ 
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    marginTop: 5,
                    marginBottom: 3
                 }}>
                     <Typography variant="h1" color="primary">Products</Typography>
                     <Button variant="contained" startIcon={<AddIcon/>} onClick={event => navigate('/dashboards/products/create')}>Add Product</Button>
                 </Box>
                <Grid container spacing={2}>
                    {butilProducts.map((product, index) =>
                        <Grid item sm={12} md={3} key={index}>
                            <ProductCard product={product} />
                        </Grid>
                    )}
                </Grid>
            </Container>

        </>
    )
}

export default Products
