import React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { ProductInterface } from 'src/models/products';
import { useNavigate } from 'react-router-dom';

interface Props {
    product: ProductInterface;
}

function ProductCard({ product }: Props) {
    const [snowMore, setShowMore] = React.useState(false);
    const navigate = useNavigate();

    return (
        <Card sx={{ maxWidth: 345 }}>
            <CardMedia
                component="img"
                alt="green iguana"
                height="200"
                image={product.image}
            />
            <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                    {product.name}
                </Typography>
                {product.description.length > 90 ? (
                    <Typography variant="body2" color="text.secondary">
                        {!snowMore ? product.description.slice(0, 100) + ' ...' : product.description} 
                        <strong onClick={e => setShowMore(!snowMore)} style={{ cursor: 'pointer' }}>{!snowMore ? ' Show more' : ' Show less'}</strong>
                    </Typography>
                 ) : (
                    <Typography variant="body2" color="text.secondary">
                        {product.description}
                    </Typography>
                )}
            </CardContent>
            <CardActions>
                <Button size="small" onClick={e => navigate('/dashboards/products/edit/' + product.productId)}>View</Button>
            </CardActions>
        </Card>
    )
}

export default ProductCard
